@import url(https://fonts.googleapis.com/css2?family=Pacifico&family=Roboto:wght@400;700&display=swap);
body{margin:0;padding:0;font-family:"Roboto",sans-serif}

.recipe-card{border-radius:5px;box-shadow:0 0 15px rgba(0,0,0,0.2);background-color:white;display:flex;flex-direction:column;justify-content:space-around;align-items:center;padding:15px;height:450px}.recipe-card img{width:250px;margin:10px 0}.recipe-title{color:#243949;font-family:"Pacifico",cursive;text-align:center;margin:0}.ingredients-text{align-self:flex-start;margin:0;text-decoration:underline}.ingredients-list{align-self:flex-start;list-style:none;padding:0}.recipe-btn{border-radius:5px;color:white;cursor:pointer;text-decoration:none;background-color:#243949;padding:7px}

.App{width:100%;min-height:100vh;background-image:linear-gradient(to right, #243949 0%, #517fa4 100%)}.recipe-cards-container{display:grid;grid-template-columns:1fr 1fr 1fr;grid-gap:50px;padding:40px}@media only screen and (min-width: 100px) and (max-width: 480px){.recipe-cards-container{grid-template-columns:1fr;padding:20px}}@media only screen and (min-width: 480px) and (max-width: 768px){.recipe-cards-container{grid-template-columns:1fr;padding:20px}}@media (min-width: 768px) and (max-width: 1024px){.recipe-cards-container{grid-template-columns:1fr 1fr;padding:30px}}

.navbar{height:70px;display:flex;flex-direction:space-between;background-color:white}.logo-side{display:flex;flex:50% 1;align-items:center;justify-content:flex-start;margin-left:40px}.logo-text{color:#517FA4;font-family:"Pacifico",cursive;margin:0}.search-form{display:flex;flex:50% 1;align-items:center;justify-content:flex-end;margin-right:40px}.search-bar{width:220px;height:25px;border:1px solid #517FA4;border-radius:5px;padding:5px;color:black;font-size:16px;margin-right:15px}::-webkit-input-placeholder{color:#517FA4}:-ms-input-placeholder{color:#517FA4}::placeholder{color:#517FA4}.search-button{border-radius:5px;color:white;cursor:pointer;width:70px;height:37px;background-color:#517FA4;font-size:16px;padding:0}@media only screen and (min-width: 100px) and (max-width: 480px){.navbar{height:50px}.logo-side{margin-left:20px}.logo-text{font-size:14px}.search-form{margin-right:20px}.search-bar{width:125px;height:10px;font-size:9px;margin-right:5px}.search-button{width:50px;height:23px;font-size:9px}}@media only screen and (min-width: 480px) and (max-width: 768px){.navbar{height:50px}.logo-side{margin-left:20px}.logo-text{font-size:19px}.search-form{margin-right:20px}.search-bar{width:150px;height:15px;font-size:12px;margin-right:5px}.search-button{width:55px;height:28px;font-size:12px}}@media only screen and (min-width: 768px) and (max-width: 1024px){.logo-side{margin-left:30px}.search-form{margin-right:30px}}

